// GENETIC STYLES FORM
.node-form {
    margin: 0;
}

.form-type-textfield,
.form-type-email {
    margin: 0 0 rem(20px);
    position: relative;

    label {
        margin-bottom: rem(6px);
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 600;
        font-size: rem(14px);
        line-height: rem(20px);
        color: $grey-700;
    }

    input {
        height: rem(44px);
        border-radius: rem(8px);
        background: $white;
        border: rem(1px) solid $grey-300;
        box-shadow: 0 rem(1px) rem(2px) rgba(16, 24, 40, 0.05);
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 400;
        font-size: rem(16px);
        line-height: rem(24px);
        color: $grey-500;
    }

    .counter {
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 500;
        font-size: rem(14px);
        line-height: rem(20px);
        text-align: right;
        color: $grey-700;
        position: absolute;
        top: 0;
        right: 0;
    }

    + p {
        margin-bottom: rem(40px);
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 400;
        font-size: rem(14px);
        line-height: rem(20px);
        color: $grey-600;
    }
}

// textarea
.form-type-textarea {
    margin: 0 0 rem(20px);

    label {
        margin-bottom: rem(6px);
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 600;
        font-size: rem(14px);
        line-height: rem(20px);
        color: $grey-700;
    }

    textarea {
        height: rem(251px);
        border-radius: rem(8px);
        background: $white;
        border: rem(1px) solid $grey-300;
        box-shadow: 0 rem(1px) rem(2px) rgba(16, 24, 40, 0.05);
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 400;
        font-size: rem(16px);
        line-height: rem(24px);
        color: $grey-500;
    }
}

// checks
.form-type-checkbox {
    margin-bottom: rem(24px);

    input {
        border-color: $grey-400;
    }

    label {
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 500;
        font-size: rem(14px);
        line-height: rem(20px);
        color: $grey-700;

        a {
            color: $blue-600;
            text-decoration: none;
        }
    }
}


.fieldgroup {
    margin: 0;

    legend {
        display: none;
    }
}



// file
.form-type-managed-file {
    > label {
        margin-bottom: rem(6px);
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 600;
        font-size: rem(14px);
        line-height: rem(20px);
        color: $grey-800;
    }

    .image-widget {
        display: flex;
    }

    .image-widget-data,
    #edit-field-idea-archivos-0 {
        width: 100%;
        margin: 0 0 rem(8px) !important;
        padding: rem(16px) rem(24px);
        border-radius: rem(12px);
        border: rem(1px) dotted $grey-300;
        background-color: $white;
        position: relative;
        overflow: hidden;
        margin: rem(10px);

        &::before {
            content: "Seleccionar imagen";
            width: rem(200px);
            height: rem(36px);
            padding-left: rem(24px);
            background-color: $white;
            background-image: url("../images/icons/icon-cloud.svg");
            background-repeat: no-repeat;
            background-size: rem(20px);
            background-position: rem(10px) center;
            border: rem(1px) solid $grey-300;
            display: inline-flex;
            justify-content: center;
            align-items: center;
            border-radius: rem(50px);
            position: relative;
            z-index: 2;
        }

        .form-file {
            width: rem(200px);
            height: rem(36px);
            position: absolute;
            top: rem(16px);
            left: rem(24px);
            margin: 0;
            padding: 0;
            font-size: rem(20px);
            cursor: pointer;
            opacity: 0;
            z-index: 4;
            filter: alpha(opacity=0);
        }
    }

    #edit-field-idea-archivos-0 {
        img {
            max-width: rem(200px);
        }
    }

    .image-preview {
        margin-right: rem(24px);

        img {
            border-radius: rem(8px);
        }

        + .image-widget-data {
            padding: 0;
            border: none;
            border-radius: 0;
            background: none;
            overflow: inherit;

            &::before {
                display: none;
            }

            img {
                display: none;
            }

            .description {
                margin-top: rem(6px);
            }

            .form-submit {
                margin-top: 0;
            }
        }
    }

    .description {
        a {
            color: $blue-600;
            text-decoration: none;

            &:hover {
                text-decoration: underline;
            }
        }
    }
}

