// chosen
// chosen
.form-type-select {
    margin: 0 0 rem(20px);

    label {
        margin-bottom: rem(6px);
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 600;
        font-size: rem(14px);
        line-height: rem(20px);
        color: $grey-700;
    }

    select {
        height: rem(44px);
        padding-right: rem(36px);
        background: $white;
        border: rem(1px) solid $grey-300;
        box-shadow: 0 rem(1px) rem(2px) rgba(16, 24, 40, 0.05);
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 500;
        font-size: rem(16px);
        line-height: rem(24px);
        color: $grey-800;

        -webkit-appearance: none;
        -moz-appearance: none;

        background-image: url("../images/icons/icon-arrow-grey.svg");
        background-repeat: no-repeat;
        background-size: rem(20px);
        background-position: calc(100% - 10px) center;
    }

    .chosen-container-single {
        .chosen-single {
            height: rem(44px);
            background: $white;
            border: none;
            box-shadow: none;
            font-family: $font-family-base;
            font-style: normal;
            font-weight: 500;
            font-size: rem(16px);
            line-height: rem(24px);
            color: $grey-500;
            display: flex;
            align-items: center;
            background-color: $white;
            background-image: url("../images/icons/icon-arrow-grey.svg");
            background-repeat: no-repeat;
            background-size: rem(20px);
            background-position: calc(100% - 10px) center;

            > div {
                display: none;
            }
        }
    }

    .chosen-container {
        height: rem(48px);
        padding: 0;
        box-shadow: none;
        background: none;

        .chosen-drop {
            width: calc(100% + 2px);
            margin-top: rem(-4px);
            margin-left: rem(-1px);
            box-shadow: none;
            border-radius: 0 0 rem(10px) rem(10px);
            border: rem(1px) solid $grey-300;

            .chosen-search {
                padding: rem(10px) rem(10px) rem(5px);

                .chosen-search-input {
                    height: 100%;
                    color: $grey-800;
                    background: none !important;
                }
            }

            .chosen-results {
                width: 100%;
            }

            .active-result {
                &.highlighted {
                    background: $grey-200 !important;
                    color: $grey-800;

                    &:hover {
                        background: $grey-200 !important;
                    }
                }
            }
        }

        &-active {
            .chosen-choices {
                border-radius: rem(10px) rem(10px) 0 0;
            }
        }
    }
}


.form-radios {
    margin-top: rem(24px);
}

.fieldgroup.form-composite {
    margin-bottom: rem(80px);

    .showmore {
        margin-top: rem(24px);
        padding-right: rem(28px);
        font-family: $font-family-base;
        font-style: normal;
        font-weight: 600;
        font-size: rem(14px);
        line-height: rem(20px);
        color: $grey-600;
        text-decoration: none;
        position: relative;
        display: inline-flex;

        &::after {
            content: "";
            width: rem(20px);
            height: rem(20px);
            background-image: url("../images/icons/icon-arrow-down-black.svg");
            background-repeat: no-repeat;
            background-size: contain;
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%) rotate(0deg); /* Inicialmente no rotada */
            transform-origin: center center;
            transition: all 0.3s ease;
        }

        &.active {
            &::after {
                transform: translateY(-50%) rotate(-180deg);
            }
        }
    }

    .form-type-checkbox {
        display: block;
        position: relative;
        margin-bottom: rem(20px);
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;

        input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
            height: 0;
            width: 0;
        }

        label {
            position: relative;
            margin-left: 0;
            padding-left: rem(24px);
            display: flex;
            font-family: $font-family-base;
            font-style: normal;
            font-weight: 500;
            font-size: rem(14px);
            line-height: rem(20px);
            color: $grey-700;
            cursor: pointer;
        }

        .checkmark {
            position: absolute;
            top: rem(2px);
            left: 0;
            height: rem(16px);
            width: rem(16px);
            border: rem(1px) solid $blue-600;
            border-radius: rem(4px);
            background-color: $blue-50;
        }

        input:checked ~ .checkmark {
            background-image: url("../images/icons/icon-checkbox-check.svg");
            background-repeat: no-repeat;
            background-size: contain;
            background-position: center;
        }

        input:checked ~ .checkmark::after {
            display: block;
        }

        // states
        &.hidden {
            display: none;
        }
    }

    .form-type-radio {
        display: block;
        margin-bottom: rem(12px);
        position: relative;
        cursor: pointer;
        -webkit-user-select: none;
        -moz-user-select: none;
        -ms-user-select: none;
        user-select: none;
        transition: all 0.3s ease;

        //padding: rem(16px);
        height: rem(56px);
        background: $white;
        border: rem(1px) solid $grey-200;
        border-radius: rem(12px);
        display: flex;
        align-items: center;

        input {
            position: absolute;
            opacity: 0;
            cursor: pointer;
            height: 0;
            width: 0;
        }

        label {
            width: 100%;
            position: relative;
            margin: 0 rem(16px);
            padding: rem(16px) rem(30px);
            display: flex;
            font-family: $font-family-base;
            font-style: normal;
            font-weight: 500;
            font-size: rem(14px);
            line-height: rem(20px);
            color: $grey-700;
            cursor: pointer;
        }

        .checkmark {
            position: absolute;
            top: rem(16px);
            left: 0;
            height: rem(20px);
            width: rem(20px);
            border: rem(1px) solid $grey-300;
            border-radius: 50%;
            background-color: $white;
        }

        input:checked ~ .checkmark {
            border-color: $blue-600;
            background-color: $blue-50;
            background-image: url("../images/icons/icon-radios-check.svg");
            background-repeat: no-repeat;
            background-size: 8px;
            background-position: center;
        }

        input:checked ~ .checkmark::after {
            display: block;
        }

        // states
        &.active {
            background-color: $blue-25;
            border-color: $blue-300;
        }
    }
}

// submits
.form-submit {
    width: auto;
    min-width: rem(108px);
    margin-top: rem(80px);
}

#edit-submit {
    margin-right: rem(16px);
    border-radius: rem(50px);
    color: $grey-700;
    border-color: $grey-300;
    background-color: $white;

    &:hover {
        border-color: $grey-300;
        background-color: $grey-50;
        color: $grey-700;
    }
}
